.board {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.game {
  width: 560px;
  height: 560px;
  display: flex;
  flex-wrap: wrap;
  background-color: rgb(255, 151, 203);

  img {
    width: 70px;
    height: 70px;
    border: 1px grey solid;
  }
}
